.carousel-root {
  width: 100% !important;
  margin: auto !important;
  /* margin-top: 3% !important; */
}

.carousel .slide {
  background: #fff !important;
  color: black;
  height: 100%;
}

/* .carousel .slide img {
  width: 109px !important;
  border-radius: 50%;
} */

.myCarousel {
  background: #faf6f4;
  margin-top: -6%;
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 6%;
  padding-bottom: 8%;
  padding-left: 5%;
  padding-right: 5%;
  /* border: 1px solid #ddd; */
      height: 340px;

}
.carousel .thumbs-wrapper {
    margin: 45px !important;
}

.carousel .control-dots {
  padding-left: 5px !important;
  outline: 0;
  bottom: -1% !important;
}
.carousel.carousel-slider .control-arrow:hover {
    background: rgb(0 0 0 / 0%) !important;
}
.myCarousel h3 {
  color: #222;
  font-weight: 100;
  letter-spacing: 0.2px;
  margin-bottom: 4px;
  font-weight: 600;
  text-transform: uppercase;
  font-size: 17px;
}

.myCarousel h4 {
  text-transform: uppercase;
  margin-top: 0;
  padding-top: 0;
  font-weight: 500;
  color: #787878;
  font-size: 14px;
}

.myCarousel p {
  font-weight: 100 !important;
  line-height: 29px !important;
  color: #222;
  font-size: 15px;
  font-family: sans-serif;
  max-height: 67px;
}

.myCarousel p:before {
  content: "“";
  color: #aaa;
  font-size: 26px;
  font-family: monospace;
  font-weight: 100;
}

.myCarousel p:after {
  content: "”";
  color: #aaa;
  font-size: 26px;
  font-family: monospace;
  font-weight: 100;
  line-height: 0;
}

.carousel .control-dots .dot {
  box-shadow: none !important;
  background: #454545 !important;
  outline: 0;
}

.carousel.carousel-slider .control-arrow {
  /* background: #000 !important; */
  height: 50px !important;
  position: absolute;
  top: 35% !important;
}

.App {
  text-align: center;
}

.carousel .control-prev.control-arrow:before  {
    border-right: 8px solid #e6c2b0 !important;
}
.carousel .control-next.control-arrow:before {
    border-left: 8px solid #e6c2b0;
}
@media only screen and (max-width: 934px) {
  .carousel-root {
    outline: 0;
    width: 93% !important;
    margin: auto !important;
  }

  .carousel.carousel-slider .control-arrow {
    display: none !important;
  }
  .myCarousel {
    background: #fafafa;
    margin-top: -9%;
    width: 88%;
    margin-left: auto;
    margin-right: auto;
    padding-top: 8%;
    padding-bottom: 12.5%;
    padding-left: 5%;
    padding-right: 5%;
    border: 1px solid #ddd;
    height: 340px;
  }

  /* .carousel .slide img {
    width: 109px !important;
    border-radius: 50%;
  } */
}